.nodeshop-closed {
    // Article Fade Out:
    .field--name-body {
        position:               relative;
        &:before {
            content:            '';
            width:              100%;
            height:             100%;    
            position:           absolute;
            left:               0;
            top:                0;
            background: -webkit-linear-gradient(top, transparent 0%,transparent 55%,$grey 100%);
            background:         linear-gradient(to bottom, transparent 0%,transparent 55%,$grey 100%);
          }
    }
    // Teaser Add Lock:
    &.teaser {
        .heading {
            &:after {
                content: "\f023";
                font-family:        "FontAwesome";
                display:            inline-block;
                color:              #aaaaaa;
                font-size:          1rem;
            }
        }
        .field--name-body:before {
            background:      transparent;
        }
    }
}